import styles from "./AudioPlayer.module.css"
import { useEffect, useRef, useState } from "react"
import { useSelector } from "react-redux"
import { calculateTime } from "../../../lib/helpers"
import { Typography } from "@mui/material"
import ControlPanel from "./ControlPanel/ControlPanel"
import { togglePlayPauseAC } from "../../../redux/playerReducer"

//www.w3schools.com html reference audio/video
const AudioPlayer = ({ context,  setAudioRef }) => {
    const [ isPlaying, setIsPlaying ] = useState(false)
    const [ duration, setDuration ] = useState(0)
    const [ currentTime, setCurrentTime ] = useState(0)

    const currentTrack = useSelector((state) => state.player.activeTrack) 
    const source = currentTrack?.track

    const audioPlayer = useRef()   // reference our audio component
    const progressBar = useRef()   // reference our progress bar
    const animationRef = useRef()  // reference the animation

    useEffect(() => {    
        setIsPlaying(false)
        // audioPlayer.current?.pause()
        audioPlayer.current?.load()
        setAudioRef(audioPlayer)
        // audioPlayer.current?.play()
    }, [currentTrack]);

    const onLoadedMetadata = () => {
        const seconds = Math.floor(audioPlayer.current.duration)
        setDuration(seconds);
        progressBar.current.max = seconds;              //      
    }

    const togglePlayPause = () => {
        const prevValue = isPlaying
        setIsPlaying(!prevValue)
        
        if (!prevValue) {
            context.resume()
            audioPlayer.current.play()
            animationRef.current = requestAnimationFrame(whilePlaying)                   //    
        } else {
            context.suspend()
            audioPlayer.current.pause()
            cancelAnimationFrame(animationRef.current)                                  //    
        }
    }

    const whilePlaying = () => {
        progressBar.current.value = audioPlayer.current.currentTime
        changePlayerCurrentTime()
        animationRef.current = requestAnimationFrame(whilePlaying)                       //       
    }

    const changeRange = () => {
        audioPlayer.current.currentTime = progressBar.current.value                         //   audio   
        changePlayerCurrentTime()
    }

    const changePlayerCurrentTime = () => {
        progressBar.current.style.setProperty('--seek-before-width', `${progressBar.current.value / duration * 100}%`)
        setCurrentTime(progressBar.current.value);
    }
    
    return (
        <div className={styles.audioPlayer}>
            <input 
                type="range" 
                defaultValue={0} 
                ref = {progressBar} 
                onChange={changeRange} 
                className={styles.progressBar}
            />
            <audio 
                preload="auto" 
                onLoadedMetadata={onLoadedMetadata} 
                className={styles.audio} 
                ref={audioPlayer} 
                src={source} 
                type="audio/mpeg" 
                controls 
            />
            <ControlPanel 
                togglePlayPause = { togglePlayPause } 
                isPlaying = { isPlaying }
                currentTrack = { currentTrack }
                audioPlayer = { audioPlayer }
                setIsPlaying = { setIsPlaying }
            />
            <Typography variant="body2" color="#fafafa" component="div">
                <div className = { styles.currentTime }>
                    { calculateTime(currentTime) }
                </div>
            </Typography>
            <Typography variant="body2" color="#fafafa" component="div">
                <div className = { styles.duration }>
                    { (duration && !isNaN(duration)) && calculateTime(duration) } {/*   nan nan   ,     */}
                </div> 
            </Typography>
        </div>
    )
}

export default AudioPlayer